<template>
  <div class="nav">
    <h1>热门文章</h1>

    <ul>
      <li  v-for="(item,index) in ArcticleData" :key="item._id">
          <i>{{index + 1}}</i>
         <router-link :to="`/actire/${item._id}`">{{item.title}}</router-link>
              </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Left",
  data() {
    return {ArcticleData:[]};
  },
  methods: {
    //获取全部数据
    async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/article",
      });
      this.ArcticleData = data.data.splice(0,3);
    },
  },
  created(){
      this.getArticles()
  }
};
</script>

<style lang="less" scoped>
.nav {
  height: 180px;
  width: 260px;
  margin-top: 10px;
  background-color: #bfe2e6;
  h1 {
    padding: 20px;
    font-size: 20px;
    font-weight: bold;
    font-family: "微软雅黑";
    border-bottom: 1px solid #e8e9e7;
  }
  ul {
    margin-top: 0px;
    margin-left: 15px;
    li {
      overflow: hidden;
      display: flex;
      height: 40px;
      padding: 0 3px;
      line-height: 40px;
      box-sizing: border-box;
      i {
        width: 22px;
        height: 22px;
        margin-top: 9px;
        margin-right: 15px;
        border-radius: 50%;
        line-height: 16px;
        font-size: 16px;
        font-style: normal;
        color: #fff;
        text-align: center;
        font-weight: 100;
        font-family: Pacifico;
      }
      i:nth-child(1) {
        background-color: #71a598;
      }
      i:nth-child(2) {
        background-color: #6eb3a3;
      }
      i:nth-child(3) {
        background-color: #93bbb2;
      }
      a {
        display: block;
        overflow: hidden;
        color: #787977;
        font-size: 14px;
        text-decoration: none;
        white-space: nowrap;
        text-overflow: ellipsis;
        flex: 1;
      }
    }
  }
}
</style>