<template>
  <div class="login">
    <el-button type="success" @click="showDialog" @click.self="hiddenDialog"
      >登录/注册</el-button
    >
  </div>
  <div class="dialog" v-if="show" @click.self="deldet">
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="登录" name="first">
          <el-form
            :model="loginFrom"
            :rules="rules"
            ref="loginFrom"
            label-width="100px"
          >
            <el-form-item label="用户名" prop="user">
              <el-input v-model="loginFrom.user"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input  type="password" v-model="loginFrom.password"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="loginsubmitForm"
                >确认登陆</el-button
              >
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册" name="second">
          <el-form
            :model="regForm"
            :rules="rules"
            ref="regForm"
            label-width="100px"
          >
            <el-form-item label="用户名" prop="user">
              <el-input v-model="regForm.user"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="regForm.password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="password2">
              <el-input type="password" v-model="regForm.password2"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="regSubmit">注册</el-button>
            </el-form-item>
          </el-form></el-tab-pane
        >
      </el-tabs>
    </div>
  </div>
</template>

<script>
import {mapMutations} from "vuex"
export default {
  name: "Logindata",
  data() {
    return {
      show: false,
      activeName: "second",
      regForm: {
        //注册表单内控件绑定的数据
        user: "",
        password: "",
        password2: "",
      },
      loginFrom: {
        user: "",
        password: "",
        password2: "",
      },
      rules: {
        //form表单里rules属性绑定的对象，用来对表单内控件做格式校验
        user: [
          { required: true, message: "请输入用户名" },
          {
            validator(rule, value, callback) {
              // validator验证器，可以对格式进行复杂限制，其中有三个参数
              if (value.length < 2 || value.length > 8) {
                callback(new Error("请输入2-8位字符"));
                //就callback 返回下面这段话
                console.log("1");
              } else {
                //字符验证
                if (/^[a-zA-Z0-9_\u4e00-\u9fa5]+$/.test(value)) {
                  callback(); //调用callback 验证成功
                } else {
                  callback(new Error("只允许汉字、字母、下划线"));
                  callback(); //
                }
              }
            },
          },
        ],
        password: [
          { required: true, message: "请输入密码" },
          {
            validator(rule, value, callback) {
              //验证字符长度
              if (value.length < 6 || value.length > 18) {
                callback(new Error("请输入6-18位字符"));
              } else {
                //字符验证
                if (/^[a-zA-Z]\w{5,17}$/.test(value)) {
                  callback();
                } else {
                  callback(new Error("以字母开头,只能包含字符、数字和下划线"));
                  callback(); //验证失败
                }
              }
            },
          },
        ],
        password2: [
          { required: true, message: "请确认密码" },
          {
            validator: (rule, value, callback) => {
              // console.log(this);
              if (this.regForm.password === value) {
                callback(); //密码一致
              } else {
                callback(new Error("两次密码不一致"));
              }
            },
          },
        ],
      },
    };
  },
  methods: {
    showDialog() {
      this.show = true;
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    hiddenDialog() {
      this.show = false;
    },
    deldet() {
      this.show = false;
    },
    // regSubmit() {
    //   //注册提交按钮 发起请求到后端
    //   this.$refs.regForm.validate(async (bool) => {
    //     //表单数据校验
    //     console.log("1111");
    //     if (bool) {
    //       let res = await this.$axios({
    //         methods: "POST",
    //         url: "/reg",
    //         data: {
    //           user: this.regForm.user,
    //           password: this.regForm.password,
    //         },
    //       });
    //       //注册成功,
    //       // 注册失败
    //     } else {
    //       return false;
    //     }
    //   });
    // },
    regSubmit() {
      //注册的提交按钮  发起请求到后端
      this.$refs.regForm.validate(async (bool) => {
        if (bool) {
          //表单数据校验成功
          let { data } = await this.$axios({
            method: "POST",
            url: "/reg",
            data: {
              user: this.regForm.user,
              password: this.regForm.password,
            },
          });
          // console.log(data);
          if (data.code) {
            //注册失败
            //后端的错误码是1，2 都是true 所以当data.code为真的时候注册失败
          } else {
            //注册成功
            this.$message.success("注册成功");
            this.activeName = "first";
            this.$refs.regForm.resetFields(); //清空表单内的数据
          }
        } else {
          return false;
        }
      });
    },
    //点击登录
    loginsubmitForm() {
      this.$refs.loginFrom.validate(async (bool) => {
        if (bool) {
          let { data } = await this.$axios({
            method: "POST",
            url: "/login",
            data: {
              user: this.loginFrom.user,
              password: this.loginFrom.password,
            },
          });
          // console.log(data);
          if (data.code) {
            this.$message.error(data.message);
          } else {
            //登录成功
            this.$message.success("登录成功");
            // this.$refs.loginFrom.resetFields(); //清空表单内的数据
            this.show = false;
            this.updatauser(data.data)
          }
        }
        else{
          return false;
        }
      });
    },
    //使用mapmutations中的updatauser
    ...mapMutations(["updatauser"])
  },
};
</script>
<style lang="less" scoped>
.dialog {
  height: 1700px;
  width: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10000;
  .content {
    position: absolute;
    top: 1200px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    padding: 10px 20px;
    background: #fff;
    height: 350px;
  }
}
</style>