<template>
  <div class="loginuser">
    <el-popover placement="bottom-end" :width="200" trigger="hover">
             
      <template #reference>
        <img :src="baseUrl + userInfo.phtoto" alt="" />
      </template>
      <div>
        <el-button type="primary" size="mini" @click="changeData"
          >修改信息</el-button
        >
        <el-button type="danger" size="mini" @click="loginOut"
          >退出登录</el-button
        >
      </div>
    </el-popover>
         <span>{{ userInfo.user }}</span>

  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "login",
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
    ...mapMutations(["cleardatauser"]),
    changeData() {
      //编程式导航
      this.$router.push("/UpdataUser");
    },
    async loginOut() {
      let { data } = await this.$axios({
        method: "POST",
        url: "/login/out",
      });
      console.log(data);
      this.$message.success(data.message);
      this.cleardatauser();
      this.$router.push("/");
    },
  },
};
</script>

<style lang="less" scoped>
.loginuser {
  width: 100px;
  height: 100%;
  margin: 8px 0;
  display: flex;
  align-items: center;
  span{
    font-size: 20px;
    font-family: cursive;
    font-weight: 400;
  }
  img {
    width: 34px;
    height: 34px;
    // background: none center center / cover;
    border-radius: 50%;
    cursor: pointer;
  }
}
</style>