import { createStore } from 'vuex'

export default createStore({
  state: {
    userInfo:{},
    addLink:0//记录添加友链
  },
  mutations: {
    // 修改数据
    updatauser(state,userInfo){
      state.userInfo = userInfo //将用户数据更新到state。userinfo中
      
    },
    //清空数据
    cleardatauser(state){
      state.userInfo = {};
    },
    nonticeAddlink(state){//每次新添加一个友链后 记录
       state.addLink++

    }
  },
  actions: {
  },
  modules: {
  }
})
