<script>
export default {  
  name: "Home",
  data() {
    return {
      ArcticleData:[],
      activeNames: ["1"],
      contactForm: {
        //表单的数据对象
        name: "",
        email: "",
        message: "",
      },
      rules: {
        //表单的验证规则   与标签中的prop属性值对应
        name: [{ required: true, message: "name值必须填写", trigger: "blur" }],
        email: [{ required: true, type: "email", trigger: "blur" }],
        message: [{ required: true, trigger: "blur" }],
      },
    };
  },
  methods: {
      //获取全部数据
    async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/article",
      });
      this.ArcticleData = data.data;
    },
    submitForm() {
      //点击提交后触发的事件
      //先验证表单里的数据格式是否符合要求
      /* 
        在html标签里写ref属性，比如ref="wanzi"
        在js中，通过this.$refs.wanzi 得到写了ref属性的dom节点。
      */
      // console.log(this.$refs.contactForm); //表单节点对象
      this.$refs.contactForm.validate( async result => {  
        //validate 表单验证 验证数据类型是否正确
        if (result) {
          //验证成功 发起请求将数据发给后端
          let {data} = await this.$axios({
            method:"POST",
            url:"/contact",
            data:this.contactForm
          })
          if(data.code) return this.$message.error(data.message)
          this.$message.success("提交反馈成功，将会在7个工作日内回复")
        } else {
          // 未验证通过
          return false;
        }
      });
    },
    handleChange(val) {
      console.log(val);
    },
  },
  created(){
    this.getArticles()
  }
};
</script>
