<template>
  <div class="nav">
    <h1>最近访客</h1>
    <ul>
      <li v-for="item in hotAricle" :key="item._id" :style="{backgroundImage:`url(${baseUrl}${item.visitor.phtoto})`}">
        <p>{{item.visitor.user}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Visitor",
  data(){
    return{
      hotAricle:[]//访客数据
    }
  },
  methods:{
     async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/visitor",
      });
      if(data.code) return;//没有访客直接return
      this.hotAricle = data.data;
    },
  },
  created(){
    this.getArticles()
  }
};
</script>

<style lang="less" scoped>
.nav {
  height: 20    0px;
  width: 260px;
  margin-top: 10px;
  background-color: #bfe2e6;
  h1 {
    padding: 15px;
    line-height: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e8e9e7;
    color: #383937;
    font-size: 16px;
    font-weight: bold;
  }
  ul {
      margin-top: 22px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
      outline: 1px black solid;
      width: 50px;
      height: 50px;
      margin-right: 10px;
      margin-bottom: 10px;
        background:  50%/cover no-repeat;

      &:nth-child(4n) {
        margin-right: 0;
      }
      p{
          padding-top:30px;
          color: #bfe2e6;
          font-weight: 200;
          font-size: 12px;
          padding-left: 10px;
      }
    }
  }
}
</style>